/*------------------------------------*    #PARAGRAPHS
\*------------------------------------*/
/**
 * The `.lede` class is used to make the introductory text (usually a paragraph)
 * of a document slightly larger: en.wikipedia.org/wiki/Lede_(news)
 */
.lede {
  font-size: 18px;
  font-size: 1.125rem;
  line-height: 1.33333;
}

/*------------------------------------*    #BLOCK
\*------------------------------------*/
/**
 * The block object simply stacks an image on top of some text-like content.
 */
/**
 * Stacked image-with-text object. A simple abstraction to cover a very commonly
 * occurring design pattern.
 */
.block {
  display: block;
}

/**
     * 1. Eliminate whitespace around images.
     */
.block__img {
  vertical-align: middle;
  /* [1] */
  margin-bottom: 24px;
}

/**
     * Text-content.
     */
.block__body {
  display: block;
}

/*------------------------------------*    #BOX
\*------------------------------------*/
/**
 * The box object simply boxes off content.
 */
/**
 * 1. So we can apply the `.box` class to naturally-inline elements.
 */
.box {
  display: block;
  /* [1] */
  padding: 24px;
}

.box > :last-child {
  margin-bottom: 0;
}

/*------------------------------------*    #BUTTONS
\*------------------------------------*/
/**
 * A simple button object.
 */
/**
 * 1. Allow us to style box model properties.
 * 2. Line different sized buttons up a little nicer.
 * 3. Make buttons inherit font styles (often necessary when styling `input`s as
 *    buttons).
 * 4. Reset/normalize some styles.
 * 5. Force all button-styled elements to appear clickable.
 * 6. Fixes odd inner spacing in IE7.
 * 7. Subtract the border size from the padding value so that buttons do not
 *    grow larger as we add borders.
 */
.btn {
  display: inline-block;
  /* [1] */
  vertical-align: middle;
  /* [2] */
  font: inherit;
  /* [3] */
  text-align: center;
  /* [4] */
  margin: 0;
  /* [4] */
  cursor: pointer;
  /* [5] */
  overflow: visible;
  /* [6] */
  padding: 11px 23px;
  /* [7] */
  background-color: #4a8ec2;
  border: 1px solid #4a8ec2;
}

.btn, .btn:hover, .btn:active, .btn:focus {
  text-decoration: none;
  /* [4] */
  color: #fff;
}

/**
 * Fix a Firefox bug whereby `input type="submit"` gains 2px extra padding.
 */
.btn::-moz-focus-inner {
  border: 0;
  padding: 0;
}

/*------------------------------------*    #FLAG
\*------------------------------------*/
/**
 * The flag object is a design pattern similar to the media object, however it
 * utilises `display: table[-cell];` to give us control over the vertical
 * alignments of the text and image. csswizardry.com/2013/05/the-flag-object
 */
/**
 * 1. Allows us to control vertical alignments
 * 2. Force the object to be the full width of its parent. Combined with [1],
 *    this makes the object behave in a quasi-`display: block;` manner.
 */
.flag {
  display: table;
  /* [1] */
  width: 100%;
  /* [2] */
}

/**
     * Items within a flag object. There should only ever be one of each.
     *
     * 1. Default to aligning content to their middles.
     */
.flag__img,
.flag__body {
  display: table-cell;
  vertical-align: middle;
  /* [1] */
}

/**
     * Flag images have a space between them and the body of the object.
     */
.flag__img {
  padding-right: 24px;
}

.flag__img > img {
  display: block;
  max-width: none;
}

/**
     * The container for the main content of the flag object.
     *
     * 1. Forces the `.flag__body` to take up all remaining space.
     */
.flag__body {
  width: 100%;
  /* [1] */
}

.flag__body,
.flag__body > :last-child {
  margin-bottom: 0;
}

/*------------------------------------*    #LAYOUT
\*------------------------------------*/
/**
 * The inuitcss layout system uses `box-sizing: border-box;` and
 * `display: inline-block;` to create an extremely powerful, flexible
 * alternative to the traditional grid system. Combine the layout items with
 * the widths found in `trumps.widths`.
 */
/**
 * Begin a layout group.
 */
.project-layout {
  list-style: none;
  margin: 0;
  padding: 0;
  margin-left: -24px;
  font-size: 0;
}

/**
     * 1. Cause columns to stack side-by-side.
     * 2. Space columns apart.
     * 3. Align columns to the tops of each other.
     * 4. Full-width unless told to behave otherwise.
     * 5. Required to combine fluid widths and fixed gutters.
     */
.project-layout__item {
  display: inline-block;
  /* [1] */
  padding-left: 24px;
  /* [2] */
  vertical-align: top;
  /* [3] */
  width: 100%;
  /* [4] */
  font-size: 16px;
}

/**
     * Layouts with tiny gutters.
     */
.project-layout--tiny {
  margin-left: -6px;
}

.project-layout--tiny > .project-layout__item {
  padding-left: 6px;
}

/**
     * Layouts with small gutters.
     */
.project-layout--small {
  margin-left: -12px;
}

.project-layout--small > .project-layout__item {
  padding-left: 12px;
}

/**
     * Layouts with large gutters.
     */
.project-layout--large {
  margin-left: -48px;
}

.project-layout--large > .project-layout__item {
  padding-left: 48px;
}

/**
     * Layouts with huge gutters.
     */
.project-layout--huge {
  margin-left: -96px;
}

.project-layout--huge > .project-layout__item {
  padding-left: 96px;
}

/**
     * Layouts with no gutters.
     */
.project-layout--flush {
  margin-left: 0;
}

.project-layout--flush > .project-layout__item {
  padding-left: 0;
}

/**
     * Reversed rendered order of layout items, e.g. items 1, 2, 3, 4 in your
     * markup will display in order 4, 3, 2, 1 on your page.
     */
.project-layout--rev {
  direction: rtl;
  text-align: left;
}

.project-layout--rev > .project-layout__item {
  direction: ltr;
  text-align: left;
}

/**
     * Align layout items to the vertical centers of each other.
     */
.project-layout--middle > .project-layout__item {
  vertical-align: middle;
}

/**
     * Align layout items to the vertical bottoms of each other.
     */
.project-layout--bottom > .project-layout__item {
  vertical-align: bottom;
}

/**
     * Make the layout items fill up from the right hand side.
     */
.project-layout--right {
  text-align: right;
}

.project-layout--right > .project-layout__item {
  text-align: left;
}

/**
     * Make the layout items fill up from the center outward.
     */
.project-layout--center {
  text-align: center;
}

.project-layout--center > .project-layout__item {
  text-align: left;
}

/**
     * Cause layout items to take up a non-explicit amount of width.
     */
.project-layout--auto > .project-layout__item {
  width: auto;
}

/*------------------------------------*    #LIST-BARE
\*------------------------------------*/
/**
 * The list-bare object simply removes any indents and bullet points from lists.
 */
.list-bare {
  margin: 0;
  padding: 0;
  list-style: none;
}

/*------------------------------------*    #LIST-BLOCK
\*------------------------------------*/
/**
 * The list-block object creates blocky list items out of a `ul` or `ol`.
 */
.list-block {
  margin: 0;
  padding: 0;
  list-style: none;
}

.list-block__item,
.list-block > li {
  padding: 24px;
}

/*------------------------------------*    #LIST-INLINE
\*------------------------------------*/
/**
 * The list-inline object simply displays a list of items in one line.
 */
.list-inline {
  margin: 0;
  padding: 0;
  list-style: none;
}

.list-inline > li {
  display: inline-block;
}

/*------------------------------------*    #LIST-UI
\*------------------------------------*/
/**
 * The UI list object creates blocky list items with a keyline separator out of
 * a `ul` or `ol`.
 */
.list-ui,
.list-ui__item,
.list-ui > li {
  border: 0 solid #ccc;
}

.list-ui {
  margin: 0;
  padding: 0;
  list-style: none;
  border-top-width: 1px;
}

.list-ui__item,
.list-ui > li {
  padding: 24px;
  border-bottom-width: 1px;
}

/*------------------------------------*    #MEDIA
\*------------------------------------*/
/**
 * Place any image- and text-like content side-by-side, as per:
 * stubbornella.org/content/2010/06/25/the-media-object-saves-hundreds-of-lines-of-code
 */
.media {
  display: block;
}

.media__img {
  float: left;
  margin-right: 24px;
}

.media__img > img {
  display: block;
}

.media__body {
  overflow: hidden;
  display: block;
}

.media__body,
.media__body > :last-child {
  margin-bottom: 0;
}

/*------------------------------------*    #PACK
\*------------------------------------*/
/**
 * The pack object simply causes any number of elements pack up horizontally to
 * automatically fill an equal, fluid width of their parent.
 */
/**
 * 1. Fill all available space.
 * 2. Cause children to be automatically equally sized.
 */
.pack {
  width: 100%;
  /* [1] */
  display: table;
  table-layout: fixed;
  /* [2] */
}

/**
     * Cause children to adopt table-like structure.
     */
.pack__item {
  display: table-cell;
}

/*------------------------------------*    #TABLES
\*------------------------------------*/
.table {
  width: 100%;
}

/*------------------------------------*    #TABS
\*------------------------------------*/
/**
 * A simple abstraction for making equal-width navigation tabs.
 */
/**
 * 1. Reset any residual styles (most likely from lists).
 * 2. Tables for layout!
 * 3. Force all `table-cell` children to have equal widths.
 * 4. Force the object to be the full width of its parent. Combined with [2],
 *    this makes the object behave in a quasi-`display: block;` manner.
 */
.tabs {
  margin: 0;
  /* [1] */
  padding: 0;
  /* [1] */
  list-style: none;
  /* [1] */
  display: table;
  /* [2] */
  table-layout: fixed;
  /* [3] */
  width: 100%;
  /* [4] */
  text-align: center;
}

.tabs__item {
  display: table-cell;
  /* [2] */
}

.tabs__link {
  display: block;
}

/*------------------------------------*    #CONTAINER
\*------------------------------------*/
.container {
  max-width: 1200px;
  margin: 0 auto;
  padding: 0 10px;
}

/*------------------------------------*    #CLEARFIX
